import app from './_firebase';

import {getFirestore} from 'firebase/firestore'
import {getStorage} from 'firebase/storage'

export const listAllContent = async () => {
  try {
    let querySnapshot = await app.firestore().collection('pre_contents').get();
    const content = querySnapshot.docs.map((doc) => {
      return {
        id: doc.id,
        ...doc.data()
      }
    })
    return content
  } catch (err) {
    console.log(err);
  }
}


export const addContent = async (values,image) => {
  try {
    console.log(values.startDate);
    let detail =" <news content-type='news'>"+ values.detail+"</news>";
    delete values.detail;
    values.content_type="news";
    values.create_at = getFirestore().FieldValue.serverTimestamp();
    let contentRef = await app.firestore().collection('pre_contents').add(values);

    let content = await contentRef.get();
    console.log("done content:"+content.id)
    contentRef.collection('detail').doc("content").set({data:detail});
    let contentData = content.data();

    if(typeof(image)=="undefined"){
      return;
    }
    var metadata = {
      contentType: 'image/jpeg'
    };
    let cover_url = "public/images/content/" + content.id;
    var storageRef = app.storage().ref();
    var uploadTask = storageRef.child(cover_url).put(image, metadata);
    uploadTask.on(getStorage().TaskEvent.STATE_CHANGED, // or 'state_changed'
      function (snapshot) {
        // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
        var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        console.log('Upload cover is ' + progress + '% done');
        switch (snapshot.state) {
          case getStorage().TaskState.PAUSED: // or 'paused'
            console.log('Upload cover is paused');
            break;
          case getStorage().TaskState.RUNNING: // or 'running'
            console.log('Upload cover is running');
            break;
        }
      },
      function (error) {

        switch (error.code) {
          case 'storage/unauthorized':
              console.log("unauthorized")
            break;
          case 'storage/canceled':
            console.log("cancel")
            break;
          case 'storage/unknown':

            console.log("unkonwn")
            // Unknown error occurred, inspect error.serverResponse
            break;
        }
      },
      function () {
        // Upload completed successfully, now we can get the download URL
        uploadTask.snapshot.ref.getDownloadURL().then(function (downloadURL) {
          console.log('File available at', downloadURL);
          contentData.cover = downloadURL;
          app.firestore().collection('pre_contents').doc(content.id).update(contentData);
        });
      });
  } catch (err) {
    console.log(err);
  }
};

export const deleteContents = (ids) => {
  try {
    ids.map((id) => {
      let cover_url = "public/images/content/" + id;
      var storageRef = app.storage().ref();
      storageRef.child(cover_url).getDownloadURL().then(onResolve, onReject);

      function onResolve(foundURL) {
        storageRef.child(cover_url).delete();
      }

      function onReject(error) {
          console.log(error.code);
      }

      app.firestore().collection('pre_contents').doc(id).collection('detail').doc('content').delete();
      app.firestore().collection('pre_contents').doc(id).delete();
    })
  } catch (err) {
    console.log(err);
  }
};

export const getContentApi = async (id) => {
  try {
    let querySnapshot = await app.firestore().collection('pre_contents').doc(id).get();
    const content = querySnapshot.data();
    content.id = id
    let querySnapshotDetail = await app.firestore().collection('pre_contents').doc(id).collection('detail').doc('content').get();
    const contentDetail = querySnapshotDetail.data()
    content.detail = contentDetail.data;
    return content
  } catch (err) {
    console.log(err);
  }
}
